
import { defineComponent } from 'vue'
import {
    IonPage,
    IonHeader,
    IonToolbar,
    IonButtons,
    IonBackButton,
    IonTitle,
    IonContent,
    IonText,
    IonButton,
    IonIcon,
} from '@ionic/vue'
import {
    logoGithub,
    logoInstagram,
    logoTwitter,
    globeOutline,
} from 'ionicons/icons'

import { useDataStore } from '@/stores/data'

export default defineComponent({
    name: 'AboutPage',
    components: {
        IonPage,
        IonHeader,
        IonToolbar,
        IonButtons,
        IonBackButton,
        IonTitle,
        IonContent,
        IonText,
        IonButton,
        IonIcon,
    },
    data() {
        return {}
    },
    methods: {
        goTo(link: string) {
            window.open(link)
        },
    },
    setup() {
        //calculating loading time
        // const t0 = performance.now()
        //define data store
        const data: any = useDataStore()
        //test data
        // console.log(data.workout.app)
        // const t1 = performance.now()
        // //displaying loading time
        // console.log(`load data took ${t1 - t0} milliseconds.`)

        return {
            logoGithub,
            logoInstagram,
            logoTwitter,
            globeOutline,
            data
        }
    },
})
